import React, { useState } from 'react';
import styled from 'styled-components';

const Container = styled.div`
    margin: 200px;
    height: 50px;
    width: 75px;
    background: #a7a7a7;
    position: relative;
    box-shadow: 5px 5px 10px -5px #a7a7a7;
    &:after,
    &:before {
        content: '';
        height: 50px;
        width: 50px;
        position: absolute;
        top: 0px;
        background: #a7a7a7;
        border-radius: 50%;
        box-shadow: 5px 5px 10px -5px #a7a7a7;
    }
    &:after {
        right: -25px;
    }
    &:before {
        left: -25px;
    }
`;
const Switch = styled.div`
    height: 40px;
    width: 40px;
    background: #f7f7f7;
    border-radius: 50%;
    left: ${props => (props.toggle ? '-20px' : '55px')};
    top: 50%;
    transform: translate(0px, -50%);
    z-index: 2;
    transition: left 0.3s ease-in-out;
    box-shadow: 0px 0px 10px -5px #f7f7f7;
`;

function Test() {
    const [toggle, setToggle] = useToggle();

    return (
        <Container onClick={setToggle}>
            <Switch toggle={toggle}></Switch>
        </Container>
    );
}

const useToggle = (initState = false) => {
    const [state, setState] = useState(initState);
    const toggle = () => setState(!state);
    return [state, toggle];
};

export default Test;
